// Navbar =======================================

$sa-navbar-bg:    $client;

$navbar-sard-toggler-icon-bg:       str-replace(url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='white' stroke-width='2.5' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e"), "#", "%23") ;

// Base styles

/* change the background color */
.navbar-sard {
    background-color: $sa-navbar-bg;
    padding: 0rem 1rem;
}

/* change the brand and text color */
.navbar-sard .navbar-brand,
.navbar-sard .navbar-text {
    color: white;
}

/* change the link color and add padding for height */
.navbar-sard .navbar-nav .nav-link {
    color: white;
    padding: $sard-spacer-14 $sard-spacer-8;
}

/* change the color of active or hovered links */

.navbar-sard .nav-item:focus .nav-link,
.navbar-sard .nav-item:hover .nav-link {
    color: #ffffff;
    background-color: darken($sa-navbar-bg, 10%); 
}
.navbar-sard .nav-item.active .nav-link{
  background-color: darken($sa-navbar-bg, 20%); /* add background-color to active links */
}


// Dropdown Menu
.navbar-sard .nav-item.dropdown.show .nav-link.dropdown-toggle{
  background-color: darken($sa-navbar-bg, 25%);
}

.navbar-sard .dropdown-menu{
  margin: 0;
  padding: 0;
  background-color: darken($sa-navbar-bg, 20%);
  color: white;
  border: none;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  overflow: hidden;
}

.navbar-sard .dropdown-menu .dropdown-item{
  padding: $sard-spacer-4 $sard-spacer-8;
  color: white;
  border-bottom: 1px solid $sa-navbar-bg;
  &:focus{
    background-color: darken($sa-navbar-bg, 25%);
  }
  &:hover{
    background-color: darken($sa-navbar-bg, 25%);
  }
  &:active{
    background-color: darken($sa-navbar-bg, 30%);
  }
}
.navbar-sard .dropdown-menu .dropdown-item:last-child{
  border-bottom: none;
}

.navbar-sard .dropdown-menu .dropdown-divider {
    height: 0;
    margin: 0;
    margin-top: -1px;
    overflow: hidden;
    border-top: 2px solid darken($sa-navbar-bg, 30%);;
}

// Navbar Toggler
.navbar-sard .navbar-toggler{
  background-color: darken($sa-navbar-bg, 20%);
  border:none;
  padding: 0 $sard-spacer-4;
  &:focus{
    background-color: darken($sa-navbar-bg, 25%);
  }
  &:hover{
    background-color: darken($sa-navbar-bg, 25%);
  }
  &:active{
    background-color: darken($sa-navbar-bg, 30%);
  }
}

.navbar-sard .navbar-toggler .navbar-toggler-icon {
  background-image: $navbar-sard-toggler-icon-bg;
  font-size: 1rem;
}

